<template>
  <tas-base-crud :config="config" />
</template>

<script>
export default {
  name: 'crud-company-grandchilds',
  data () {
    return {
      config: {
        title: 'Cucu Perusahaan',
        model_api: 'company-grandchilds',
        getter: 'company-grandchilds',
        setter: 'company-grandchilds',
        pk_field: 'grandchild_code',
        permission: {
          create: 'create-company-grandchilds',
          read: 'view-company-grandchilds',
          update: 'update-company-grandchilds',
          delete: 'delete-company-grandchilds'
        },
        fields: [
          {
            id: 'active',
            data: 'active',
            label: 'Status',
            placeholder: null,
            methods: {
              list: {
                order: true,
                class: { 0: 'badge badge-danger', 1: 'badge badge-primary' },
                transform: 'active'
              },
              detail: {
                order: true,
                class: { 0: 'badge badge-danger', 1: 'badge badge-primary' },
                transform: 'active'
              },
              create: {
                view_data: 'active',
                type: 'radio',
                value: '1',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Aktif', code: '1' }, { label: 'Non Aktif', code: '0' }]
                  }
                }
              },
              update: {
                view_data: 'active',
                type: 'radio',
                value: '1',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Aktif', code: '1' }, { label: 'Non Aktif', code: '0' }]
                  }
                }
              },
              filter: {
                view_data: 'active',
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Aktif', code: '1' }, { label: 'Non Aktif', code: '0' }]
                  }
                }
              }
            }
          },
          { id: 'id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'updated_at', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'updated_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_at', label: 'Dibuat', methods: { create: false, update: false, filter: false } },
          { id: 'created_by', label: 'Dibuat Oleh', methods: { list: false, create: false, update: false, filter: false } },
          {
            id: 'division_id',
            label: 'Divisi',
            methods: {
              create: {
                setter: 'divisions',
                getter: 'divisions',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'division_name', code: 'id', display: ['division_name']
                  }
                }
              },
              update: {
                setter: 'divisions',
                getter: 'divisions',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'division_name', code: 'id', display: ['division_name']
                  }
                }
              },
              filter: {
                setter: 'divisions',
                getter: 'divisions',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'division_name', code: 'id', display: ['division_name']
                  }
                }
              }
            }
          },
          { id: 'grandchild_code', label: 'Kode Cucu' },
          { id: 'grandchild_name', label: 'Nama Cucu' }
        ]
      }
    }
  }
}
</script>



